/* apply a natural box layout model to all elements, but allowing components to change */
html {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*, *:before, *:after {
  box-sizing: inherit;
}

/*------------------------------------------------------------------------*\
	LIST RE-WORK
\*------------------------------------------------------------------------*/
ul {
  padding-left: 1em;
  text-indent: -1em;
  margin-left: 0;
}
ul li:before {
  content: "•";
  text-indent: -1em;
  padding-right: 0.75em;
}
ul li {
  text-indent: -1em;
}

.case-log ul, .milestone-list, .portal-bar ul, .login-list, nav.top-nav ul {
  padding-left: 0;
  text-indent: 0;
  list-style: none;
  margin-bottom: 0;
}
.case-log ul li, .milestone-list li, .portal-bar ul li, .login-list li, nav.top-nav ul li {
  text-indent: 0;
}
.case-log ul li::before, .milestone-list li::before, .portal-bar ul li::before, .login-list li::before, nav.top-nav ul li::before {
  content: none;
}

/*------------------------------------------------------------------------*\
	STANDARD VIDEO EMBED CONTAINER 16x9 (VIMEO ETC)
\*------------------------------------------------------------------------*/
.embed-container {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  max-width: 100%;
}

.embed-container iframe,
.embed-container object,
.embed-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/*------------------------------------------------------------------------*\
	BACKGROUNDS
\*------------------------------------------------------------------------*/
.case-handler-details__portrait-containter::after, .banner {
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.milestone-list li::before, .case-handler-details__email::before, .case-handler-details__phone::before, .portal-tile__icon, .logout-link::before, .profile-link::before {
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
}

/*------------------------------------------------------------------------*\
	CURSOR
\*------------------------------------------------------------------------*/
.button-link,
a.button-link,
a:link.button-link, .paginationjs li a, .swpm-login-widget-form input[type=submit].swpm-login-form-submit, a.portal-tile, button, input[type=submit] {
  cursor: pointer;
  cursor: hand;
}

